<template>
    <div id="wrapper">
        <navbar id="nav" class="animate__animated animate__fadeInLeft"  style="display: none;"/>
        <div class="d-flex flex-column animate__animated animate__fadeInRight" id="content-wrapper" style="display: none !important;">
            <div id="content">
                <header-btn/>
                <div class="container-fluid text-dark">
                    <h3 class="text-dark text-right nrt">      </h3>
                    <hr>
                    <div class="row nrt" dir="rtl" style="overflow-x: auto;">
                        <label class="col-sm-1 mt-1 no-print">  : </label>
                        <select class="form-control col-sm-1 no-print" v-model="month">
                            <option value="1">1</option>
                            <option value="2">2</option>
                            <option value="3">3</option>
                            <option value="4">4</option>
                            <option value="5">5</option>
                            <option value="6">6</option>
                            <option value="7">7</option>
                            <option value="8">8</option>
                            <option value="9">9</option>
                            <option value="10">10</option>
                            <option value="11">11</option>
                            <option value="12">12</option>
                        </select>

                        <label class="col-sm-1 mt-1 no-print">  : </label>
                        <input type="number" class="form-control col-sm-1 no-print" v-model="year">

                        <button class="btn btn-success mx-3 no-print" v-on:click="search()">  <i class="fa fa-search"></i> </button>
                        
                        <div class="col-xl-12 col-md-12 col-sm-12" v-for="staff in staffs" :key="staff.st_id">
                            <label class="float-right my-3">   : {{ staff.staff_name }} </label>
                            <table class="table table-bordered table-striped text-dark text-center mt-2">
                                <thead> 
                                    <th style="width: 7%">  </th>
                                    <th style="width: 25%">  </th>
                                    <th style="width: 15%">    </th>
                                    <th style="width: 18%">     </th>
                                    <th style="width: 18%">    </th>
                                </thead>
                                <tbody>
                                    <tr v-for="employee in employees.filter(obj => obj.staff_name == staff.staff_name)" :key="employee.emp_id">
                                        <td> {{ employee.emp_id }} </td> 
                                        <td> {{ employee.full_name }} </td>
                                        <td>
                                            {{ employee.total_fine | fine_filter }} 
                                            <span> IQD </span>
                                        </td>
                                        <td> 
                                            {{ employee.total_overtime | money_filter }} 
                                            <span v-if="employee.salary_type == 'Monthly'"> $ </span>
                                            <span v-else> IQD </span>
                                        </td>
                                        <td> 
                                            {{ employee.gived_salary | money_filter }} 
                                            <span v-if="employee.salary_type == 'Monthly'"> $ </span>
                                            <span v-else> IQD </span>
                                        </td>
                                    </tr>
                                </tbody>
                            </table>
                            <hr>
                        </div>
                    </div>
                    
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import auth from '../../auth';
    export default {
        name: "givedSalaryReport",
        data() {
            return {
                month: new Date().getMonth(),
                year: new Date().getFullYear(),
                employees: [],
                staffs: []
            }
        },
        beforeCreate() {
            auth("givedSalaryReport");
        },
        created() {
            this.$http.post("/staff/getData").then(({data}) => {
                this.staffs = data;
            });
        },
        methods: {
            search() {
                this.$http.post("/gived_salary/getGivedSalary/" + this.month + "/" + this.year).then(({data}) => {
                    this.employees = data;
                });
            }
        },
        filters: {
            money_filter(value) {
                var num = value.toString().split("").reverse();
                var arr = [];
                for (let i = 0; i < num.length; i++) {
                    arr.unshift(num[i]);
                    if((i + 1) % 3 == 0){
                        arr.unshift(",");
                    }
                }
                var num_with_comma = arr.join("");
                if(num_with_comma.charAt(0) == ','){
                    return num_with_comma.slice(1);
                }
                return num_with_comma;
            },
            fine_filter(value) {
                if(value == "0.00"){
                    return 0;
                }
                return value;
            }
        }
    }
</script>

<style scoped>
    @font-face {
        font-family: nrt;
        src: url("../../assets/fonts/NRT-Reg.ttf");
    }
    .nrt {
        font-family: nrt;
    }
    td {
        padding: .5rem;
        direction: ltr;
    }
    @media print {
        .no-print{
            display: none;
        }
    }
</style>